.input {
    position: relative;
    
    &__label {
        &-text {
            display: block;
            position: absolute;
            left: 5px;
            top: -0.6em;
            background-color: $white;
            font-size: 0.8em;
            padding: 0 3px 0 3px;
        }
        
        &_error {
            color: $error-color;
            &:focus::placeholder{
                // transform: translateY(-100px);
                display: none;
            }
        }
    }

    &__control {
        outline: none;
        padding: 8px 25px 8px 16px;
        border: 0.5px solid $border-color;
        border-radius: 4px;
        min-width: 241px;

        &_full-width {
            width: 100%;
        }

        &:focus {
            @include setAlpha(border-color, $violet, 1);
        }
        
        &_error {
            border: 0.5px solid $error-color;
            &:focus {
                @include setAlpha(border-color, $error-color, 1);
            }
        }
    }

    &__icon {
        width: 16px;
        height: 16px;
        position: absolute;
        top: 50%;
        right: 9px;
        transform: translateY(-50%);
    }
}